import sound from './sound'
import store from '@/store/index'
import animation from  './animation'

let intervalID
let matchFound = false




const tiles = {
  alternate: true,
  dest: {square: null, id:null },
  source: {square: null, id:null},
}

const invalidMove = tiles => {
  sound.badMove.play()
  tiles.source.square.parentElement.classList.remove('selected')
  tiles.source.square = tiles.dest.square
  tiles.source.square.parentElement.classList.add('selected')
  tiles.source.id = tiles.dest.id
  tiles.dest.id = null
  tiles.dest.square = null
}

const swapAmount = tiles => {
  const temp = tiles.source.square.dataset.amount
  tiles.source.square.dataset.amount = tiles.dest.square.dataset.amount
  tiles.dest.square.dataset.amount = temp
}

const swapImg = tiles => {
  const dest = tiles.dest.square
  const source = tiles.source.square
  const destClass =  dest.classList[1]
  const sourceClass = source.classList[1]

  source.removeAttribute('style')
  source.parentElement.removeAttribute('style')
  source.parentElement.classList.remove('selected')
  source.classList.replace(sourceClass, destClass)
  
  dest.removeAttribute('style')
  dest.parentElement.removeAttribute('style')
  dest.classList.replace(destClass, sourceClass)
}


const setTiles = square => {
  const destID = parseInt(square.getAttribute('id'))
  if(tiles.source.square !== null && tiles.dest.square === null && tiles.source.square !== square ){
    tiles.dest.id = destID
    tiles.dest.square = square
    validMoves()
  }else return
}

const validMoves = async() => {
  store.commit('madeFirstMove', true)
  let moves = [tiles.source.id - 1, tiles.source.id + 1, tiles.source.id+ 8, tiles.source.id - 8]
  let valid = moves.includes(tiles.dest.id)
  if(valid)swapTiles()
  else invalidMove(tiles)
}

const checkForMatch  = async () => {
  if(!store.state.crush.pause)while(findMatch()){await animateFound()}
}

const lvlInfo = () => {
  const currentLvl = store.getters.getCrushState('currentLvL')
  const levels = store.getters.getCrushState('levels')
  return levels[currentLvl-1]
}

const add = tiles =>{  
  let total = 0
  const data = lvlInfo()
  const grid = store.getters.getCrushState('grid')
  tiles.forEach(index => total+= parseInt(grid[index].dataset.amount))

  if(total === data.target)addPoints(tiles, grid)
}


const addPoints =  (streak, grid) => {
  streak.forEach(id => {
    grid[id].dataset.amount = 0
    grid[id].classList.add('marked')
  })

  store.commit('increaseCombo')
  matchFound = true
}

const getTilesByClassName = (className, reverse = false, arr = []) => {
  const tiles = document.getElementsByClassName(className)
  Array.from(tiles).forEach(tile => arr.push(tile))
  if(reverse)arr.reverse()
  return arr
}

const updateClass = tiles => {
  tiles.forEach(tile => {
    tile.removeAttribute('class')
    tile.classList.add(...switchClassName(tile.dataset.amount))
  })
}

export const switchClassName = className => {
  let css = ''
  switch(parseInt(className)) {
    case 5:
      css = ['coin', 'five']
      break
    case 10:
      css = ['coin', 'ten']
      break
    case 20:
      css = ['coin', 'twenty']
      break
    case 50:
      css = ['coin', 'fifty']
      break
    case 100:
      css = ['coin', 'one-hundred']
      break
    case 500:
      css = ['coin', 'five-hundred']
      break
    default:
      css = ['coin', 'bg-dark']
      break
  }
  return css
}


const resetTiles = tiles => {
  tiles.source.square = tiles.source.id = null
  tiles.dest.square = tiles.dest.id = null
  tiles.alternate = true
}

const removeAnimation = tiles => {
  tiles.forEach(tile => {
    tile.removeAttribute('style')
    tile.removeAttribute('data-down')
    addHoverCss() 
  })
}

const addHoverCss = () => {
  const tiles = document.getElementsByClassName('tile-container')
  Array.from(tiles).forEach(tile =>tile.classList.add('hvr-grow'))
}

/**
* @param {object} game contains current information like number of points awarded, lvl, & target to match
* @desc loops through the grid and finds any pair of 3 that matches the target value
* @return {boolean} if points is greater than 0 return TRUE else return FALSE                    
**/
const findMatch = () => {
  matchFound =false
  for(let i=0; i<62; i++){

    const threeRows = [      6, 7,         14, 15,        22, 23,         30, 31,         38, 39,         46, 47,         54, 55]
    const fourRows =  [   5, 6, 7,     13, 14, 15,    21, 22, 23,     29, 30, 31,     37, 38, 39,     45, 46, 47,     53, 54, 55,     61, 62, 63]
    const fiveRows =  [4, 5, 6, 7, 12, 13, 14, 15, 20,21, 22, 23, 28, 29, 30, 31, 36, 37, 38, 39, 44, 45, 46, 47, 52, 53, 54, 55, 60, 61, 62, 63]


    if(i<48)add([i, i+8, i+8*2])
    if(i<40)add([i, i+8, i+8*2, i+8*3])
    if(i<32)add([i, i+8, i+8*2, i+8*3,i+8*4])

    if(threeRows.includes(i)) continue
    add([i, i+1, i+2])

    if(fourRows.includes(i)) continue
    add([i, i+1, i+2, i+3])

    if(fiveRows.includes(i)) continue
    add([i, i+1, i+2, i+3, i+4])
  }

  return matchFound
}


const animateFound = async () => {
  const grid = store.getters.getCrushState('grid')
  const markedArr = getTilesByClassName('marked', true)
  const [replaceArr, validArr] = markedTiles(markedArr, grid)
  await animation.growAndExplode(markedArr)
  await animation.drop(validArr)
  sound.drop.play()

  updateTilesAmount(replaceArr, grid)
  const updateArr = getTilesByClassName('update', false)
  const invalidArr = getTilesByClassName('replace', false)
  updateClass(updateArr)
  createTiles(invalidArr, true)
  validArr.forEach(tile => tile.removeAttribute('style'))

  await animation.grow(invalidArr)
  sound.wellDone.play()
  store.commit('increaseGameTime')
  store.commit('increasePoints')
  removeAnimation(replaceArr)
}


const markedTiles = (tiles, grid) => {
  let validArr = [], replaceArr = []
  tiles.forEach(tile => {
    let ID = parseInt(tile.getAttribute('id'))
    while(ID >= 0){
      replaceArr.push(grid[ID])
      if(parseInt(grid[ID].dataset.amount) > 0)validArr.push(grid[ID])
      ID -=8
    }
  })
  
  replaceArr = [...new Set(replaceArr)] //remove duplicates tiles
  validArr = [...new Set(validArr)]    //remove duplicates tiles
  calculateDown(validArr, grid)
  return [replaceArr, validArr]
}


const calculateDown = (tiles, grid) => {
  tiles.forEach(tile => {
    let down = 0, tileID = parseInt(tile.getAttribute('id'))
    while(tileID < 64){
      const tileValue = parseInt(grid[tileID].dataset.amount)
      if(tileValue === 0)down++
      tileID += 8
    }
    tile.setAttribute('data-down', down)
  })
}


const updateTilesAmount = (tiles, grid) => {
  const gridTop = [0,1,2,3,4,5,6,7]
  tiles.forEach(tile => {
    const tileID = parseInt(tile.getAttribute('id'))
    let currentID = tileID
    while(currentID >= 0){
      currentID -= 8
      if(gridTop.includes(tileID) || currentID < 0){    //reach the top || gone out of bounce
        tile.classList.add('replace') 
        break
      }
      if(parseInt(grid[currentID].dataset.amount) > 0){
        tile.dataset.amount = grid[currentID].dataset.amount
        grid[currentID].dataset.amount = 0
        tile.classList.add( 'update') 
        break
      }
    }
  })
}
  

const swapTiles = async () => {
  swapAmount(tiles)
  if(findMatch())tiles.alternate = false
  await animation.swap(tiles)
  sound.swap.play()

  if(!tiles.alternate){
    swapImg(tiles)
    await animateFound()
  }else swapAmount(tiles)

  resetTiles(tiles)
  while(findMatch()){await animateFound()}
}


const introSound = () => new Promise((resolve, _reject) =>{
  setTimeout(()=>{
  sound.getReady.play()
  sound.getReady.on('end',() =>{
    sound.gameStart.play()
    sound.gameStart.on('end', ()=> resolve())
  })
  },1000)
})



const gameOver = async() => {
  clearTimer()
  const tiles= []
  const temp = document.getElementsByClassName('tile-container')
  Array.from(temp).forEach(tile => tiles.push(tile.firstElementChild))

  store.commit('pushCurrentScore')
  store.commit('startGame', false)
  store.commit('updateUser', {prop: 'scores', value: store.getters.getCrushState('currentScore')})

  sound.timeUp.play()
  sound.timeUp.on('end', function(){sound.gameOver.play()});

  await animation.shrinkAndRotate(tiles)
  store.commit('toggleModal', 'tryAgain')
}


const lvlCompleted = async() =>{
  clearTimer()
  store.commit('pushCurrentScore')
  store.commit('startGame', false)
  
  if(store.state.crush.currentLvL == store.state.crush.maxLvl){
    sound.gameCompleted.play()
    store.commit('toggleModal', 'Completed')
    return
  }
  
  const tiles= []
  const temp = document.getElementsByClassName('tile-container')
  Array.from(temp).forEach(tile => tiles.push(tile.firstElementChild))
  sound.lvlComplete.play()
  await animation.shrinkAndRotate(tiles)
  store.commit('toggleModal', 'Continue')
}


const countDown = () =>{
  if(!intervalID && store.getters.getCrushState('playing'))
    intervalID = setInterval(progressCountDown, 1500)
}


const progressCountDown = async () => {
  if(!store.getters.getPauseStatus && store.getters.getCrushState('playing')){
    const target =  document.getElementById('progress-bar-container').firstElementChild
    let time = parseInt(target.style.width)
    time -= store.getters.getCrushObjState({obj: 'clock', prop: 'minus'})
  
    if(time < 20 && time > 0){
      sound.warning.play()
      target.classList.replace('bg-warning', 'bg-danger')
      target.parentElement.classList.add('pulse')
    }
    else {
      target.classList.replace('bg-danger', 'bg-warning')
      target.parentElement.classList.remove('pulse')
    }  
    
    store.commit('increasePlayTime')
    if(time == 0)gameOver()
    target.style.width = `${time}%`
  }
}


export const onClick = (e) => {
  const square = e.target.closest('.coin') 
  if(tiles.source.square === null){
    sound.select.play()
    tiles.source.square = square
    square.parentElement.classList.add('selected')
    tiles.source.id = parseInt(square.getAttribute('id'))
  }else setTiles(square)
}


export const onSwipe = (direction, mouseEvent) =>{
  if(tiles.source.square === null){
    tiles.source.square = mouseEvent.srcElement.closest('.coin')
    tiles.source.id = parseInt(tiles.source.square.getAttribute('id'))
    tiles.source.square.parentElement.classList.add('selected')
  }else if(tiles.dest.square !== null) return
  
  if(direction === 'top') setTiles(document.getElementById(tiles.source.id-8))
  else if(direction === 'left')setTiles(document.getElementById(tiles.source.id-1))
  else if(direction === 'right')setTiles(document.getElementById(tiles.source.id+1))
  else if(direction === 'bottom')setTiles(document.getElementById(tiles.source.id+8))
}


/** 
  * @param {Array} tiles  array of elements that needs a needs value
  * @param {boolean} addOn a boolean value that determines whether to add css-class invisible to tiles or not
  * @desc loops through each tile and assign a random amount and corresponding css class
**/
export const createTiles = (tiles, addOn = false) => {
  const currency = store.getters.getCurrency
  tiles.forEach(tile => {
    try{
      if(tiles.length > 64)throw new Error
      tile.dataset.amount = 0
      const rand =  Math.floor(Math.random() * currency.length)
      tile.removeAttribute('class')
      tile.setAttribute('data-amount', currency[rand].amount)
      tile.classList.add('coin',currency[rand].css)
      if(addOn)tile.classList.add('invisible')
    }catch{tile.parentElement.classList.add('bg-white')}
  })
}


/**
  * @param {Array} grid	empty array that will contains each game tile present on the board
  * @desc   loops through document and find each element with class tile-container
  *         add data-amount attr to each element 
  *         then push each element to the grid array 
**/
export const createGrid = (className) => {
  const tiles = document.getElementsByClassName(className)
  Array.from(tiles).forEach(tile =>{
    tile.firstElementChild.setAttribute('data-amount', 0)
    store.commit('setGrid', tile.firstElementChild)
  })  
}

export const createNewGame = async ()=>{
  createTiles(store.getters.getCrushState('grid'))
  await animation.grow(store.getters.getCrushState('grid'))
  startGame(store.getters.getPauseStatus)
}


export const toggleCoins = async () => { 
  const btn = document.getElementById('pauseBtn')

  if(store.getters.getPauseStatus)
    btn.innerText = 'resume'
  else {
    sound.welcomeBack.play()
    btn.innerText = 'pause'
  }

  const targets = document.getElementsByClassName('tile-container')
  Array.from(targets).forEach(tile => {
    tile.firstElementChild.removeAttribute('class')
    if(!store.getters.getPauseStatus)tile.firstElementChild.classList.add(...switchClassName(tile.firstElementChild.dataset.amount))
    else tile.firstElementChild.classList.add('coin', 'penny')
  })  
}



export const startGame = async pause =>{ 
  if(!pause && !store.getters.getCrushState('playing')){
    store.commit('startGame', true)
    await introSound()
    checkForMatch()  
    countDown()
  }

}


export const checkScores = async () => {
  const level = lvlInfo()
  const currentScore = store.getters.getCrushState('currentScore')
  const highScore = store.state.user.highScore
  
  if(currentScore == level.goal)lvlCompleted()
  if(currentScore > highScore)store.commit('updateUser', {prop:'highScore', value: currentScore})
}


export const clearTimer = () =>{
    clearInterval(intervalID)
    intervalID = null
    store.commit('startGame', false)
}



export const restartGame = async() =>{
  clearTimer()
  store.commit('startGame', false)
  store.commit('pushCurrentScore')
  store.commit('pauseGame')
  store.commit('madeFirstMove', false)
  createNewGame()

  const target= document.getElementById('progress-bar-container').firstElementChild
  document.getElementById('pauseBtn').innerText = 'pause'
  target.style.width = `100%`
  store.state.crush.currentScore = 0

}
