<template>
  <div class="d-flex flex-column  align-items-center h-100 ">
    <h2 class="fw-bold text-start fs-5 mt-0">Scores</h2>
    <div class="content-box flex-grow-1 overflow-auto px-2 px-sm-3 w-100">
      <table class="table">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">Date</th>
            <th scope="col">Month</th>
            <th scope="col">Score</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(score, index) in getScore" :key="index"  :class="score.colour">
            <th scope="row">{{index}}</th>
            <td>{{score.day}}</td>
            <td>{{score.month}}</td>
            <td>{{score.score}}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>



<script setup>
  import {onMounted, onBeforeUnmount} from 'vue'
  import {mapGetters, mapMutations} from '@/libs/vuex'
  import {toggleCoins} from '@/views/partials/math-crush/game-helpers'

  const { getObjState, getScore} = mapGetters()
  const {enableScroll, disableScroll,  resetModal, pauseGame} = mapMutations()

  
  onMounted(()=>disableScroll())

  onBeforeUnmount(_ => {
    pauseGame()
    toggleCoins()
    enableScroll()
    resetModal()
  })

</script>


<style scoped>
  .score:hover, .score:hover p, .score:hover span{
    background-color: white;
    color: black !important;
    font-weight: bold;
  }


  .content-box::-webkit-scrollbar {
    width: 7px;
  }

  /* Track */
  .content-box::-webkit-scrollbar-track {
    background: rgb(187, 185, 185); ;
  }

  /* Handle*/
  .content-box::-webkit-scrollbar-thumb, .content-box::-webkit-scrollbar-thumb:hover {
    background: var(--bs-warning);
  }
</style>