<template>
  <div class="d-flex flex-column justify-content-center align-items-center h-75 ">
    <img src="@/assets/img/icon-sm-dark.png" alt="logo">
    <h3 class="my-4 fs-5">Do you want to restart</h3>
    <div class="d-flex justify-content-center my-3">
      <button @click="[toggleModal(null),  pauseGame(), toggleCoins()]" type="button" class="fs-7 btn btn-warning py-1 mx-2 text-white text-uppercase">No</button>
      <button @click="[toggleModal(null),  restartGame()]" type="button" class="fs-7 btn btn-success py-1 mx-2 text-white text-uppercase">yes</button>
    </div>
  </div>
</template>


<script setup>
  import {mapMutations} from '@/libs/vuex'
  import { onBeforeUnmount, onMounted} from 'vue'
  import {toggleCoins, restartGame} from '@/views/partials/math-crush/game-helpers'

  const {resetModal, toggleModal, disableScroll, enableScroll, pauseGame} = mapMutations()

  onMounted(()=> disableScroll())
  
  onBeforeUnmount(()=> {
    enableScroll()
    resetModal()
  })



</script>