<template>
  <div class="d-flex flex-column justify-content-center align-items-center h-90 ">
    <img src="@/assets/img/icon-sm-dark.png" alt="logo">
    <h3 class="mt-4 fw-bold fs-5">Level Failed</h3>
    <h3 class="my-3 fs-5">your score is: <span class="text-danger">{{getCrushState('currentScore')}}</span></h3>
    <div class="d-flex justify-content-center my-2">
      <button @click="[toggleModal(null), redirect({route: 'dashboard', timeOut: 100})]" type="button" class="fs-7 bg-warning btn px-4 py-1 mx-2 text-white text-uppercase">quit</button>
      <button @click="[toggleModal(null), restartGame()]" type="button" class="fs-7 btn bg-success  py-1 mx-2 text-white text-uppercase">Try Again</button>
    </div>
  </div>
</template>


<script setup>
  import {onBeforeUnmount, onMounted} from 'vue'
  import {mapGetters, mapMutations} from '@/libs/vuex'
  import {restartGame} from '@/views/partials/math-crush/game-helpers'
  const {getCrushState} = mapGetters()
  const {resetModal, toggleModal, redirect,enableScroll, disableScroll, pauseGame} = mapMutations()


  

  onMounted(()=>{
    pauseGame()
    disableScroll()
  })

  onBeforeUnmount(_ => {
    enableScroll()
    resetModal()
  })
  

</script>