import sound from './sound'

const growAndExplode = async (tiles) => {
  const transition =  anime.timeline({
    targets: [...tiles],
    easing: 'easeInSine',
  })

  transition.add({
    scale: 1.3,
    duration: 700,
    complete: () => {
      sound.explode.play()
      tiles.forEach(tile => {
        tile.removeAttribute('class')
        tile.classList.add('coin', 'bomb') 
      })
    }
  })

  transition.add({
    duration: 700,
    complete: () => {
      tiles.forEach(tile => {
        tile.classList.add('invisible')
        tile.removeAttribute('style')
      })
    }
  })

  await transition.finished
}


const drop = async tiles => {
  const height = document.querySelector('.tile-container').getBoundingClientRect().height;

  await Promise.all(tiles.map(async tile => {
    const down = tile.dataset.down

    const transition =  anime.timeline({
      duration: 500,
      direction: 'normal',
      easing: 'easeInSine',
    })
    
    transition.add({
      targets: tile,
      endDelay: 200,
      translateY: height*down
    })

    await transition.finished
  }))
}




const grow = async tiles => {
  const transition = anime.timeline({
    targets: [...tiles],
    direction: 'normal',
    easing: 'linear',
  })

  transition.add({ 
    scale: 0,
    delay: 180,
    duration: 0,
  })

  transition.add({
    scale: 1,
    // rotate: '+=1turn',
    duration: 200,
    begin: () => tiles.forEach(tile => tile.classList.remove('invisible'))
  })

  await transition.finished
}

const shrinkAndRotate = async tiles => {

  const transition = anime.timeline({
    targets: [...tiles],
    direction: 'normal',
    easing: 'linear',
  })

  transition.add({ 
    rotate: '+=1turn',
    scale: 0,
    duration: 700,
    delay: 300,
    complete: () => {
      tiles.forEach(tile => {
        tile.classList.add('invisible')
        tile.removeAttribute('style')
      })
    }
  })
  await transition.finished
}


const swap = async tiles =>{
  tiles.source.square.parentElement.classList.remove('selected')
  const width = document.querySelector('.coin').parentElement.getBoundingClientRect().width;
  const height = document.querySelector('.coin').parentElement.getBoundingClientRect().height;

  if(tiles.dest.id === (tiles.source.id-8)) await slideUp(height, tiles)
  else if(tiles.dest.id === (tiles.source.id-1)) await slideLeft(width, tiles)
  else if(tiles.dest.id === (tiles.source.id+1)) await slideRight(width, tiles)
  else if(tiles.dest.id === (tiles.source.id+8)) await slideDown(height, tiles)
}


const slideUp = async (distance, tiles) =>{
  const transition = anime.timeline({
    duration: 500,
    easing: 'easeInSine',
    direction: (tiles.alternate) ? 'alternate' : 'normal'
  })

  transition.add({
    translateY: -distance,
    targets: tiles.source.square
  })

  transition.add({
    translateY: distance,
    targets: tiles.dest.square
  },0)

  await transition.finished
}


const slideDown = async (distance, tiles) =>{
  const transition = anime.timeline({
    duration: 500,
    easing: 'easeInSine',
    direction: (tiles.alternate) ? 'alternate' : 'normal'
  })

  transition.add({
    translateY: distance,
    targets: tiles.source.square
  })

  transition.add({
    translateY: -distance,
    targets: tiles.dest.square
  },0)

  await transition.finished
}




const slideLeft = async (distance, tiles) =>{
  const transition = anime.timeline({
    duration: 500,
    easing: 'easeInSine',
    direction: (tiles.alternate) ? 'alternate' : 'normal'
  })

  transition.add({
    translateX: -distance,
    targets: tiles.source.square
  })

  transition.add({
    translateX: distance,
    targets: tiles.dest.square
  },0)

  await transition.finished
}



const slideRight = async (distance, tiles) =>{
  const transition = anime.timeline({
    duration: 500,
    easing: 'easeInSine',
    direction: (tiles.alternate) ? 'alternate' : 'normal'
  })

  transition.add({
    translateX: distance,
    targets: tiles.source.square
  })

  transition.add({
    translateX: -distance,
    targets: tiles.dest.square
  },0)

  await transition.finished
}


const animation = {swap, drop, growAndExplode, grow, shrinkAndRotate}
export default animation
