<template>
  <div class="container-fluid px-0 bg-light ">
    <MainNav />
    
    <teleport to="#modal-root">
      <Modal>
        <keep-alive>
          <component :is="getObjState({obj:'modal', prop:'current'})"></component>
        </keep-alive>
      </Modal>
    </teleport>

    <main class="d-flex mt-1">
      <SideNav />
      <div class="flex-grow-1">
        <router-view v-slot="{ Component }">
            <component :is="Component" mode="out-in"/>
        </router-view>
      </div>
    </main>
  </div>
</template>




<script>
  import {mapGetters} from '@/libs/vuex'

  import MainNav from '@/components/navbars/main-nav.vue'
  import SideNav from '@/components/navbars/side-nav.vue'
  import Modal from '@/components/modals/modal-container.vue'
  
  //Games Modal
  import Completed from '@/components/modals/game-completed-modal.vue'
  import Continue from '@/components/modals/nextLvl-modal.vue'
  import TryAgain from '@/components/modals/tryAgain-modal.vue'
  import Restart from '@/components/modals/restart-modal.vue'
  import Rules from '@/components/modals/rules-modal.vue'
  import Scores from '@/components/modals/scores-modal.vue'
  

  export default{
    name: 'main-layout',
    components: { MainNav, Modal, SideNav, Rules, Completed, Continue, Restart, TryAgain, Scores},
    setup(){
      const {getObjState} = mapGetters()
      return {getObjState}
    },

    async created(){
      const { games } = this.$store.state
      const { user } = this.$store.state

      if(!games.length) await this.$store.dispatch('fetchGame')
      if(!user.length) await this.$store.dispatch('fetchUserData')
    } 
  }
</script>


<style scoped>
  main{height: 91.5vh;}
</style>